.App {
  --expandable-sidebar-create-snippet-width: 280px;

  --expandable-sidebar-tags-width: 150px;

  --classic-snippet-layout-snippet-list-width: 350px;

  --sidebar-width: 65px;

  --topbar-height: 60px;

  --sidebar-color: rgb(32, 32, 32);

  --expandable-sidebar-create-snippet-color: rgb(43, 43, 43);

  --topbar-color: rgb(43, 43, 43);

  --snippet-editor-header-height: 45px;

  --snippet-editor-footer-height: 45px;
}

.App-blurrables {
  height: 100%;
}

.App-create-modal-expanded .App-blurrables {
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

.Content {
  height: calc(100%);
}

.App-topbar-expanded .Content {
  height: calc(100% - var(--topbar-height));
}

.CreateSnippetModal .ProseMirrorWriter-toolbar-container {
  top: -55px;
  left: -14px;
}

.CreateSnippetModal-save-button {
  margin-top: 10px;
}



