.EditableLabel-read {
  /* font-style: italic; */
  cursor: pointer;
  opacity: 0.5;
}

.EditableLabel-read:hover {
  opacity: 0.7;
}

.EditableLabel-read-placeholder {
  opacity: 0.2;
}

.EditableLabel-read-placeholder:hover {
  opacity: 0.3;
}

.EditableLabel-write {
  width: 80px;
  border: none;
}

.EditableLabel-write:focus {
  outline: none;
}



