.ExpandableSidebar-tags {
  -ms-flex-align: center;
      align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.TagsSidebar-entry {
  color: white;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  border: 1px solid white;
  border-radius: 10px;
  font-size: 15px;
  padding-top: 1px;
  padding-left: 6px;
  margin: 4px;
  cursor: pointer;
  display: block;
}

.TagsSidebar-entry:focus {
  outline-width: 2px;
}

.TagsSidebar-entry-selected {
  background-color: white;
  color: var(--sidebar-color);
}



