div.ProseMirror[contenteditable="true"]:focus {
  outline: none;
}

.ProseMirror * {
  margin: 0px;
}

.ProseMirrorReader * {
  margin: 0px;

  line-height: 22px;
}

.menubar {
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.menubar.visible {
  opacity: 1;
}

.ProseMirrorWrapper-menu-item {
  margin-right: 8px;
  opacity: 1;
  font-size: 12px;
  cursor: pointer;
}

.ProseMirrorWriter-editor-and-toolbar-container {
  height: 100%;
}

.ProseMirrorWriter-toolbar-container {
  height: 0px;
  position: relative;
}

.ProseMirrorWriter-editor-container {
  position: relative;

  line-height: 22px;
}

.ProseMirror-menubar {
  /* TODO */
  display: none;
}

.ProseMirrorWrapper-menu-item-icon {
  position: relative;
  top: 2px;
  font-size: 15px;
}



