.Topbar {
  height: 0px;
  background-color: var(--topbar-color);
  -webkit-transition: height 0.1s;
  -o-transition: height 0.1s;
  transition: height 0.1s;
  overflow: hidden;
  text-align: center;
  -webkit-box-shadow: inset 0 0 20px #000000;
          box-shadow: inset 0 0 20px #000000;
}

.App-topbar-expanded .Topbar {
  height: var(--topbar-height);
}

.Topbar-search-input {
  color: white;
  background-color: var(--topbar-color);
  border: none;
  outline: none;
  font-size: 20px;
  padding: 6px;
  border-radius: 4px;
  font-style: italic;
  font-weight: lighter;
  width: 300px;
  margin: 0px;
  margin-top: 9px;
}



