.ClassicSnippetLayout {
  height: 100%;
}

.ClassicSnippetLayout-editor-view {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 40px;
  height: 100%;

  -webkit-transition: width 0.1s;

  -o-transition: width 0.1s;

  transition: width 0.1s;

  float: left;
  width: calc(
    100% - var(--sidebar-width) -
      var(--classic-snippet-layout-snippet-list-width)
  );
}

/* SNIPPET LIST STYLING */
.ClassicSnippetLayout-snippet-list {
  margin-top: 40px;
  float: left;
  width: var(--classic-snippet-layout-snippet-list-width);
  height: calc(100% - 40px);
  overflow: scroll;
}

.ClassicSnippetLayout-snippet-list-entry-untitled {
  opacity: 0.5;
}

.ClassicSnippetLayout-snippet-list-entry-outer {
  border: 4px solid rgba(236, 236, 236, 0);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px;
  margin-top: -1px;
}

.ClassicSnippetLayout-snippet-list-entry-outer:hover,
.ClassicSnippetLayout-snippet-list-entry-outer-selected {
  border: 4px solid rgba(236, 236, 236, 1);
}

.ClassicSnippetLayout-snippet-list-entry-inner {
  border: 1px solid rgba(216, 216, 216, 0.3);
  border-radius: 5px;
  font-size: 15px;
  overflow: hidden;
  position: relative;
  padding-left: 10px;
}

.ClassicSnippetLayout-snippet-list-entry-inner.ClassicSnippetLayout-snippet-list-entry-type-richtext,
.ClassicSnippetLayout-snippet-list-entry-inner.ClassicSnippetLayout-snippet-list-entry-type-todo {
  line-height: 20px;
  max-height: 115px;
}

.ClassicSnippetLayout-snippet-list-entry-title {
  padding: 5px;
  padding-bottom: 0px;
  padding-left: 8px;
  font-size: 15px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  opacity: 0.5;
}

.ClassicSnippetLayout-snippet-list-entry-title .material-icons {
  position: relative;
  top: 5px;
  font-size: 20px;
  margin-top: -10px;
  margin-right: 5px;
}

.ClassicSnippetLayout-snippet-list-entry-type-richtext .material-icons,
.ClassicSnippetLayout-snippet-list-entry-type-todo .material-icons {
  color: rgb(255, 153, 0);
}

.ClassicSnippetLayout-snippet-list-entry-type-other .material-icons {
  color: rgb(0, 83, 159);
}

.ClassicSnippetLayout-snippet-list-entry-content {
  padding: 10px;
  padding-top: 8px;
  overflow: hidden;
}

/* LAYOUT RELATED STYLING */
.ClassicSnippetLayout .SnippetEditor {
  height: 100%;
}

.ClassicSnippetLayout .SnippetEditor::before {
  display: none;
}

.ClassicSnippetLayout .SnippetEditor-file-view {
  border: none;
  padding: 0px;
  /* margin: 0px; */
  height: calc(
    100% - var(--snippet-editor-header-height) -
      var(--snippet-editor-footer-height)
  );
}

.ClassicSnippetLayout .SnippetEditor-file-view .CodeMirror {
  height: calc(100%);
}

.ClassicSnippetLayout
  .SnippetEditor-file-view
  .ProseMirrorWriter-editor-container {
  height: 100%;
  overflow-y: scroll;
}

/* dont extend the clickable area downwards and to the right */
.ClassicSnippetLayout .SnippetEditor-footer:hover.SnippetEditor-footer::before,
.ClassicSnippetLayout .SnippetEditor-footer::before {
  bottom: 0px;
  right: 0px;
}

.ClassicSnippetLayout .ProseMirrorWriter-toolbar-container {
  top: -30px;
}

/* SIDEBAR RELATED STYLING */
.App-expandable-sidebar-create-snippet-expanded
  .ClassicSnippetLayout-editor-view {
  width: calc(
    100% - var(--sidebar-width) -
      var(--classic-snippet-layout-snippet-list-width) -
      var(--expandable-sidebar-create-snippet-width)
  );
}

.App-expandable-sidebar-tags-expanded .ClassicSnippetLayout-editor-view {
  width: calc(
    100% - var(--sidebar-width) -
      var(--classic-snippet-layout-snippet-list-width) -
      var(--expandable-sidebar-tags-width)
  );
}



