body {
  margin: 0;
  padding: 0;
  font-family: "Questrial", sans-serif;
  font-size: 16px;
  color: rgb(55, 55, 55);
}

html,
body,
#root,
.Main {
  height: 100%;
}

h1,
h2,
h3,
h4,
a,
b,
strong {
  color: rgb(255, 185, 80);
}

/* override codemirror font */
div.cm-s-neo pre,
.CodeMirror-linenumber {
  font-family: Consolas, Monaco, "Andale Mono", monospace;
}

/* override user agent margin */
pre {
  margin: 0px;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-thumb {
  background: rgb(179, 179, 179);
}

.CodeMirror-hscrollbar {
  height: 3px;
}



