/* Sidebar */
.Sidebar {
  padding-top: 15px;
  width: var(--sidebar-width);
  height: calc(100% - 15px);
  float: left;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: var(--sidebar-color);
}

.Sidebar i {
  outline: none;
}

.Sidebar-button {
  margin-left: 16px;
  font-size: 30px;
  cursor: pointer;
  color: white;
}

.Sidebar-button-vertical-split {
  font-size: 28px;
  padding-left: 2px;
}

.Sidebar-button-note {
  font-size: 24px;
  padding-left: 3px;
}

.Sidebar-button-search {
  font-size: 33px;
  margin-left: 18px;
  margin-top: 4px;
}

.ExpandableSidebar {
  width: 0px;
  overflow: hidden;
  -webkit-transition: width 0.1s;
  -o-transition: width 0.1s;
  transition: width 0.1s;
}

.App-expandable-sidebar-create-snippet-expanded
  .ExpandableSidebar-create-snippet {
  width: var(--expandable-sidebar-create-snippet-width);
}

.App-expandable-sidebar-tags-expanded .ExpandableSidebar-tags {
  width: var(--expandable-sidebar-tags-width);
}

.ExpandableSidebar-create-snippet {
  height: 100%;
  float: left;
  background-color: var(--expandable-sidebar-create-snippet-color);
  -webkit-box-shadow: inset 0 0 20px #000000;
          box-shadow: inset 0 0 20px #000000;
}

.ExpandableSidebar-tags {
  height: 100%;
  float: left;
  background-color: var(--expandable-sidebar-create-snippet-color);
  -webkit-box-shadow: inset 0 0 20px #000000;
          box-shadow: inset 0 0 20px #000000;
}

.ExpandableSidebar-create-snippet-button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  padding-top: 5px;
  padding-left: 7px;
  padding-right: 7px;
  font-size: 11px;
  display: inline-block;
  border-radius: 4px;
  height: 57px;
  width: calc(100% - 220px);
  margin: 10px;
  margin-left: 110px;
  border: 1px solid;
}

.ExpandableSidebar-create-snippet-button-code {
  border-color: rgb(0, 83, 159);
  color: rgb(0, 83, 159);
  background-color: rgb(245, 250, 255);
}

.ExpandableSidebar-create-snippet-button-code::-webkit-input-placeholder {
  color: rgb(0, 83, 159);
}

.ExpandableSidebar-create-snippet-button-code:-ms-input-placeholder {
  color: rgb(0, 83, 159);
}

.ExpandableSidebar-create-snippet-button-code::-ms-input-placeholder {
  color: rgb(0, 83, 159);
}

.ExpandableSidebar-create-snippet-button-code::placeholder {
  color: rgb(0, 83, 159);
}

.ExpandableSidebar-create-snippet-button-note {
  border-color: rgb(255, 193, 37);
  color: rgb(255, 193, 37);
  background-color: rgb(252, 249, 240);
}

.ExpandableSidebar-create-snippet-button-note::-webkit-input-placeholder {
  color: rgb(255, 193, 37);
}

.ExpandableSidebar-create-snippet-button-note:-ms-input-placeholder {
  color: rgb(255, 193, 37);
}

.ExpandableSidebar-create-snippet-button-note::-ms-input-placeholder {
  color: rgb(255, 193, 37);
}

.ExpandableSidebar-create-snippet-button-note::placeholder {
  color: rgb(255, 193, 37);
}

.ExpandableSidebar-create-snippet-input {
  font-size: 19px;
  outline: none;
  padding: 10px;
  border-radius: 4px;
  font-style: italic;
  font-weight: lighter;
  height: 45px;
  resize: none;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: calc(100% - 20px);
  margin: 10px;
}



