.Modal-outer {
  position: fixed;
  z-index: 10;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.Modal-inner {
  z-index: 20;
  margin: 5% auto;
  padding: 20px;
  padding-top: 5px;
  width: 50%;
  border: 1px solid #888;
  background-color: white;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 80px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 80px rgba(0, 0, 0, 0.5);
}



