.SnippetEditor {
}

/* Extend hoverable area with pseudo element */
.SnippetEditor::before {
  position: absolute;
  top: 10px;
  right: -10px;
  left: -10px;
  bottom: -10px;
  content: "";
  /* border: 1px solid; */
}

.SnippetEditor:hover::before {
  top: -10px;
  right: -30px;
  left: -30px;
  bottom: -10px;
}

.SnippetEditor-header {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px;
  padding-top: 15px;
  padding-left: 10px;
  height: var(--snippet-editor-header-height);

  opacity: 0.5;
  -webkit-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  transition: opacity 0.1s;
}

.SnippetEditor-footer {
  position: relative;
  height: var(--snippet-editor-footer-height);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 10px;

  opacity: 0.5;
  -webkit-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  transition: opacity 0.1s;
}

.SnippetEditor:hover .SnippetEditor-header,
.SnippetEditor:focus-within .SnippetEditor-header {
  opacity: 1;
  -webkit-transition: opacity 0.15s;
  -o-transition: opacity 0.15s;
  transition: opacity 0.15s;
}

.SnippetEditor:hover .SnippetEditor-footer,
.SnippetEditor:focus-within .SnippetEditor-footer {
  opacity: 0.7;
  -webkit-transition: opacity 0.15s;
  -o-transition: opacity 0.15s;
  transition: opacity 0.15s;
}

.SnippetEditor-header-menu {
  position: relative;
}

.SnippetEditor-not-focused.SnippetEditor-richtext-mode
  .SnippetEditor-header-menu,
.SnippetEditor-focused.SnippetEditor-richtext-mode .SnippetEditor-header-files {
  display: none;
}

.SnippetEditor-upper-right-corner {
  float: right;
}

.SnippetEditor-file-view {
  border: 1px solid rgba(216, 216, 216, 0.3);
  padding: 25px;
  border-radius: 5px;
  overflow-x: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.SnippetEditor:hover .SnippetEditor-footer:hover,
.SnippetEditor:focus-within .SnippetEditor-footer:focus-within {
  position: relative;
  opacity: 1;
}

/* Extend hoverable area with pseudo element */
.SnippetEditor-footer::before {
  position: absolute;
  content: "";
}

.SnippetEditor-footer:hover.SnippetEditor-footer::before {
  top: -10px;
  right: -30px;
  left: -30px;
  bottom: -10px;
}

.SnippetEditor-footer input {
  position: relative;
  top: -1px;
  border: 1px solid rgba(216, 216, 216, 0.6);
  border-radius: 15px;
  padding-top: 1px;
  padding-bottom: 2px;
  margin-left: 5px;
  width: 30px;
  text-align: center;
  outline: none;
  -webkit-transition: width 0.1s;
  -o-transition: width 0.1s;
  transition: width 0.1s;
}

.SnippetEditor-footer input:focus {
  width: 70px;
}

.SnippetEditor-title {
  padding-right: 15px;
}

.SnippetEditor-tag {
  font-size: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 3px;
  padding-top: 1px;
  border: 1px solid rgba(216, 216, 216, 0.4);
  border-radius: 15px;
  margin-left: 5px;
}

.SnippetEditor-tag > i {
  position: relative;
  top: 2px;
  font-size: 13px;
  margin-right: 3px;
  opacity: 0.5;
}

.SnippetEditor-tag .Buttons-basic-button i {
  font-size: 13px;
  width: 12px;
}

.SnippetEditor-tag,
.SnippetEditor-file {
  padding-right: 5px;
}

.SnippetEditor-file-selected .EditableLabel-read {
  border-bottom: 1px solid black;
}



