.Buttons-basic-button {
  margin-left: 4px;
  opacity: 0.5;
  cursor: pointer;
}

.Buttons-basic-button:hover {
  opacity: 0.7;
}

.Buttons-basic-button .material-icons {
  position: relative;
  width: 21px;
}

.Buttons-15 {
  top: 2px;
  font-size: 15px;
}

.Buttons-20 {
  top: 4px;
  font-size: 20px;
}

.Buttons-basic-sidebar-button > i {
  margin-left: 16px;
  font-size: 30px;
  color: rgba(0, 83, 159, 0.65);
}



