.TodoList {
  --width: 250px;
  border: 1px solid rgba(216, 216, 216, 0.3);
  border-radius: 5px;
  width: var(--width);
  margin-left: calc((100% - var(--width)) / 2);
  margin-right: calc((100% - var(--width)) / 2);
  padding: 20px;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;

  overflow: hidden;
}

.TodoList-entry {
  display: block;
  padding: 1px;
  margin-bottom: 3px;
}

.TodoList-checkbox {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
  width: 20px;
  height: 20px;
  margin: 0px;
  padding: 0px;
  margin-top: -5px;
  margin-right: 5px;
  padding-top: 3px;

  outline-offset: -5px;
}

.TodoList-editing .TodoList-checkbox {
  cursor: pointer;
}

.TodoList-checkbox i {
  font-size: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.TodoList-read-only .TodoList-checkbox i {
  opacity: 0.75;
}

.TodoList-entry-done .TodoList-checkbox i.TodoList-checkbox-open-indicator {
  display: none;
}

:not(.TodoList-entry-done)
  .TodoList-checkbox
  i.TodoList-checkbox-done-indicator {
  display: none;
}

.TodoList-entry-done .TodoList-checkbox i.TodoList-checkbox-done-indicator {
  display: inline-block;
}

.TodoList-read-only .TodoList-checkbox {
  outline: none;
}

.TodoList-entry-input-field {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  outline: none;
  width: calc(100% - 20px - 5px);
}

.TodoList-entry-done .TodoList-entry-input-field {
  text-decoration: line-through;
}



