.SnippetLayout-outer {
  overflow-y: scroll;
  padding-top: 40px;
  height: calc(100% - 40px);
}

.SnippetLayout-outer hr {
  margin: 0px;
}

.SnippetLayout-inner {
  margin: 5% auto;
  margin-top: 0px;
  margin-bottom: 0px;
  /* this is set to 51% instead of 50% to avoid showing a horizontal scrollbar at the bottom */
  width: 51%;
}

.SnippetLayout .SnippetEditor-todo-mode .SnippetEditor-file-view {
  border: none;
  padding: 0px;
}

hr {
  opacity: 0.2;
}

.SnippetLayout-snippet-outer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.SnippetLayout-snippet-inner {
  position: relative;
  /* this has been commented so the clickable area extension of the  footer can expand downwards.
     remove it later if it isn't causing any impediments */
  /* overflow: hidden;  */
}

.SnippetLayout-loading-indicator {
  background-color: white;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.7;
  z-index: 1;
}

.SnippetLayout-spinner {
  /* this styling is similar to the button styling */
  position: relative;
  opacity: 0.5;
  top: 2px;
  font-size: 15px;
  width: 21px;
  padding-right: 5px;
}

.SnippetLayout-rotating {
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

.SnippetEditor-file-view {
  position: relative;
}

.SnippetLayout-outer .ProseMirrorWriter-toolbar-container {
  top: -55px;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



